@import url(../../fontello-9b0df39d/css/pwebsite.css);
header {
  display: flex;
  justify-content: space-between;
  width: 100%; }
  header h1 a {
    margin: 50px 0 0;
    display: flex;
    flex-wrap: wrap;
    width: 350px;
    color: white;
    text-decoration: none; }
    header h1 a span {
      width: 100%; }
      header h1 a span:nth-of-type(1) {
        font-size: 50px; }
      header h1 a span:nth-of-type(2) {
        font-size: 30px;
        font-weight: 300; }
  header span.apology {
    position: absolute;
    top: 100px;
    right: 200px;
    color: white;
    background: #E11246;
    padding: 10px;
    font-size: 12px; }
  header nav ul {
    display: flex;
    padding: 0;
    margin: 50px 0 0;
    overflow: hidden; }
    header nav ul li {
      list-style: none;
      position: relative;
      transition: 800ms;
      right: 0; }
      header nav ul li.expanded {
        right: -1000px; }
      header nav ul li a {
        text-decoration: inherit;
        padding: 5px 15px;
        height: 100%;
        color: white;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center; }
        header nav ul li a.active {
          border-bottom: 4px solid #E11246; }
    header nav ul button {
      color: white;
      background: #e11246;
      position: relative;
      border: none;
      display: flex;
      justify-content: space-around;
      align-items: center;
      font-size: 30px;
      padding: 5px 10px;
      border-radius: 5px;
      outline: none;
      z-index: 999; }
      header nav ul button span {
        padding-right: 25px; }
      header nav ul button img {
        transition: 500ms;
        transform: rotate(0deg); }
        header nav ul button img.expanded {
          transform: rotate(45deg); }

@media screen and (max-width: 1050px) {
  header {
    margin-bottom: 0;
    flex-wrap: wrap; }
    header h1 {
      width: 100%;
      text-align: center; }
    header nav {
      width: 100%;
      margin: 0px auto; }
      header nav ul {
        justify-content: center; } }

@media screen and (max-width: 650px) {
  header h1 {
    text-align: center; }
  header nav {
    width: 350px;
    margin: 0px auto; }
    header nav ul {
      flex-wrap: wrap; }
      header nav ul li {
        width: 50%;
        height: 40px;
        text-align: center; } }

@media screen and (max-width: 499px) {
  header nav {
    width: 175px;
    margin: 0px auto; }
    header nav ul {
      flex-wrap: wrap; }
      header nav ul li {
        width: 100%;
        height: 40px;
        text-align: center; } }
